<template>
    <div class="pt-5">
        <div class="container">
            <div>
                <h2>Проекты</h2>
                <p>Список инвестиционных проектов</p>
            </div>
            <hr class="mb-5">
            <div class="row mb-5">
                <div class="col-md-4">
                    <b-form-group label="Город">
                        <b-form-select v-model="filter.city" class="mb-1">
                            <option value="">Не выбрано</option>
                            <option v-for="item in getParams.city" :value="item.name">{{ item.name }}</option>
                        </b-form-select>
                    </b-form-group>
                </div>
                <div class="col-md-4">
                    <b-form-group label="Стадия">
                        <b-form-select v-model="filter.stage" class="mb-1">
                            <option value="">Не выбрано</option>
                            <option v-for="item in getParams.stage" :value="item.name">{{ item.name }}</option>
                        </b-form-select>
                    </b-form-group>
                </div>
                <div class="col-md-4">
                    <b-form-group label="Индустрия">
                        <b-form-select v-model="filter.industry" class="mb-1">
                            <option value="">Не выбрано</option>
                            <option v-for="item in getParams.industry" :value="item.name">{{ item.name }}</option>
                        </b-form-select>
                    </b-form-group>
                </div>
                <div class="col-md-6">
                    <b-form-group label="Необходимые инвестиции:" class="mb-0">
                        <div class="row">
                            <div class="col-md-1 my-auto">
                                От:
                            </div>
                            <div class="col-md-5">
                                <b-form-input v-model="price.first" type="number" required placeholder="От"></b-form-input>
                            </div>
                            <div class="col-md-1 my-auto">
                                До:
                            </div>
                            <div class="col-md-5">
                                <b-form-input v-model="price.last" type="number" required placeholder="До"></b-form-input>
                            </div>
                        </div>
                    </b-form-group>
                </div>
                <div class="col-md-2 mt-auto ml-auto">
                    <b-button variant="primary" style="width: 100%" @click="submitFilter">Отправить</b-button>
                </div>
                <div class="col-md-2 mt-auto">
                    <b-button @click="reset" variant="outline-primary" style="width: 100%">Сбросить</b-button>
                </div>
            </div>
            <div v-if="!getProjects.length" class="row py-5" style="text-align: center">
                <h3>Проекты не найдены</h3>
            </div>
            <div v-else class="row py-5">
                <div class="col-md-4 mb-4" style="display: flex" v-for="item in getProjects" :key="item.id">
                    <b-card
                            :title="item.title"
                            :img-src="item.img"
                            img-alt="Image"
                            img-top
                            tag="article"
                            class="mb-2"
                            :footer="'Email: ' + (item.email)"
                            style="width: 100%;"
                    >
                        <ul class="pl-3">
                            <li>Город: <b>{{ item.params.city }}</b></li>
                            <li>Стадия: <b>{{ item.params.stage }}</b> </li>
                            <li>Категория: <b>{{ item.params.industry }}</b> </li>
                            <li><b>{{ item.params.price }} ₽</b></li>
                        </ul>
                        <p class="card-text">
                            {{ item.subtitle }}
                        </p>
                        <div slot="footer">
                            <div>{{ item.email }}</div>
                            <b-button :to="{ name: 'project', params: { id: item.id }}" variant="outline-primary" class="mt-3" style="width: 100%">Подробнее</b-button>
                        </div>
                    </b-card>
                </div>
            </div>
            <div v-if="getPagination" class="row">
                <div class="col-md-12 d-flex justify-content-center pb-5">
                    <ul class="pagination b-pagination pagination-md">
                        <li v-for="(item, index) in getPagination.pages" @click="paginateHandler(item)" :key="`${index}-pag3262835`" class="page-item d-none d-sm-flex" :class="{ 'active': paginate.page === item  }">
                            <span tabindex="-1" class="page-link" v-text="item"></span>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapActions, mapState } from 'vuex';

    export default {
        name: "Projects",
        data() {
            return {
                filter: {
                    city: '',
                    stage: '',
                    industry: '',
                },
                price: {
                    first: 0,
                    last: 9999999
                },
                load: {
                    params: false,
                    projects: false
                },
                paginate: {
                    page: 1,
                    items: 6
                },
                filterNormalize: {}
            }
        },
        computed: {
            ...mapState({
                'getProjects': state => state.projects.message,
                'getPagination': state => state.projects.paginate,
                'getParams': state => state.params
            })
        },
        methods: {
            ...mapActions([
                'loadParams',
                'toggleLoad',
                'loadProjects'
            ]),
            submitFilter() {
                this.paginate.page = 1;
                this.filterNormalize = this.filter;
                this.filterNormalize.price = `${this.price.first}-${this.price.last}`;

                this.loadProjectsWrap();
            },
            paginateHandler(page) {
                this.paginate.page = page;

                this.loadProjectsWrap();
            },
            loadProjectsWrap() {
                const query = Object.assign(this.filterNormalize, this.paginate);

                this.load.projects = false;
                this.checkLoad();

                this.loadProjects({ params: query }).finally(() => {
                    this.load.projects = true;
                    this.checkLoad();
                });
            },
            checkLoad() {
                this.toggleLoad(!(this.load.params && this.load.projects));
            },
            reset() {
                this.filter.city = '';
                this.filter.stage = '';
                this.filter.industry = '';
                this.price.first = 0;
                this.price.last = 9999999;
                this.paginate.page = 1;

                this.loadProjectsWrap();
            }
        },
        created() {
            this.toggleLoad(true);
            this.loadParams().finally(() => {
                this.load.params = true;
                this.checkLoad();
            });

            this.loadProjectsWrap();
        }
    }
</script>
